<template>
	<div id="shop-coupon" :class="`shop-${shop_style}`">
		<public-header title="代金券"></public-header>
		<div class="content font-14" >
			<van-tabs
				v-model="tabActive"
				@change="onChangeStatus"
				color="#FF2d4B"
				title-active-color="#FF2d4B"
				title-inactive-color="#000"
				:line-width="80"
				:line-height="2"
				:offset-top="46"
				sticky
			>
				<van-tab v-for="(item, index) in tabs" :key="index">
					<div slot="title" class="tab-title">
						{{item.status_cn}}
					</div>
					<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
						<template v-if="!records.empty">
							<van-list
								v-model="records.loading"
								:finished="records.finished"
								@load="onLoad"
								:offset="100"
								:immediate-check="false"
							>
								<div class="coupon-item" v-for="(item, index) in records.data" :key="item.id">
									<div class="left">
										<div>
											<span class="font-14">{{Lang.dollarSign}}</span><span class="discount">{{item.discount}}</span>
										</div>
										<div class="margin-5-t ">满{{item.condition}}元使用</div>
									</div>
									<div class="right">
										<div class="w-100 ellipsis font-14 font-bold"><i class="icon icon-crownfill"></i>{{item.title}}</div>
										<div class="w-100 ellipsis c-gray margin-10-tb">全场通用</div>
										<div class="w-100 ellipsis c-gray">有效期至{{item.endtime}}</div>
										<template v-if="item.status == 1">
											<router-link :to="util.getUrl({path: '/shop/pages/home/index', query: {sid: item.sid}})" class="btn-collect" tag="div">
												立即使用
											</router-link>
										</template>
									</div>
								</div>
							</van-list>
							<div class="loaded" v-if="records.finished">
								<div class="loaded-tips">我是有底线的</div>
							</div>
						</template>
						<template v-else>
							<div class="no-data">
								<img src= "static/img/coupon_no_con.png" alt="" />
								<p>您没有代金券</p>
							</div>
						</template>
					</van-pull-refresh>
				</van-tab>
<!--
				<van-tab title="待领取">
