							<div class="btn-collect">立即领取</div>
						</div>
					</div>
				</van-tab>
				<van-tab title="未使用">
					<div class="coupon-item" v-for="(item, index) in 10" :key="index">
						<div class="left">
							<div>
								<span class="font-14">{{Lang.dollarSign}}</span><span class="discount">3.5</span>
							</div>
							<div class="margin-5-t ">满59元使用</div>
						</div>
						<div class="right">
							<div class="w-100 ellipsis font-14 font-bold"><i class="icon icon-crownfill"></i>门店进店领券活动优惠券</div>
							<div class="w-100 ellipsis c-gray margin-10-tb">全场通用</div>
