							<div class="w-100 ellipsis c-gray margin-10-tb">全场通用</div>
							<div class="w-100 ellipsis c-gray">有效期至2020-03-31 23:59</div>
						</div>
					</div>
				</van-tab>
-->
			</van-tabs>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getData, setData, getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				sid: 0,
				status: 0,
				tabs: [
					{
						status: 1,
						status_cn: '未使用',
						num: 0,
					},
					{
						status: 2,
						status_cn: '已使用',
						num: 0,
					},
					{
						status: 3,
						status_cn: '已到期',
						num: 0,
					},
				],
				tabActive: 0,
				filter: {
					items: {
						status: 1
					}
				},
				isRefresh: false,
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				showPreLoading: true,
				shop_style: ''
			}
		},
		components: {
			PublicHeader,
		},
		methods: {
			onLoad(force) {
				var that = this;
				getRecords({
					vue: that,
					force: force,
					url: 'wmall/shop/coupon/list',
					recordsName: 'coupons',
					data: {
						sid: that.sid
					},
					success: function(result) {
						this.shop_style = result.store.data.shop_style;
					},
					fail: function() {
					}
				});
			},

			onChangeStatus(index) {
				var that = this;
				that.filter.items.status = that.tabs[index]['status'];
				this.onLoad(true);
			},

			onPullDownRefresh() {
				this.onLoad(true);
			},

		},

		created() {
			var query = this.util.parseQuery(this.$route.query);
			this.sid = query.sid;
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	@import "../../../static/css/shop.css";
	#shop-coupon .coupon-item{
		margin: 10px 15px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		background-color: #EEF9F1;
		font-size: 12px;
		min-height: 10px;
		border-radius: 5px;
		border: 1px solid #D6E2D8;
		overflow: hidden;
	}
	#shop-coupon .coupon-item.kabao{
		background-color: #FFEA9B;
		border-color: #E1D8BA;
	}
	#shop-coupon .coupon-item .left{
		width: 30%;
		height: 100%;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		color: #3FB765;
	}
	#shop-coupon .coupon-item.kabao .left{
		color: #333333;
	}
	#shop-coupon .coupon-item .right{
		position: relative;
		width: 70%;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: space-around;
		padding: 15px 10px;
		background-color: #fff;
		border-left: 1px solid #D6E2D8;
	}
	#shop-coupon .coupon-item .discount{
		font-size: 24px;
		font-weight: bold;
	}
	#shop-coupon .coupon-item .btn-collect{
		position: absolute;
		z-index: 1;
		right: 10px;
		top: 50%;
		transform: translateY(-50%);
		background-color: #36C85A;
		color: #fff;
		border-radius: 50px;
		padding: 7px 10px;
	}
	#shop-coupon .coupon-item.kabao .btn-collect{
		background-color: #EBD170;
		color: #333;
	}
	#shop-coupon .kabao-bind{
		color: #3FB765;
	}
	#shop-coupon .coupon-item .icon-crownfill{
		display: none;
	}
	#shop-coupon .coupon-item.kabao .icon-crownfill{
		display: inline;
		margin-right: 5px;
		color: #EBD170;
	}


</style>


